import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router";
import DatPhongFinder from "../../../apis/DatPhongFinder";
import {
  convertTime,
  NormalizeDate,
  NumberFormat,
} from "../../../utils/DataHandler";
import CTDPSuDungDichVu from "./CTDPSuDungDichVu";
const ChiTietDatPhong = () => {
  const { id } = useParams();
  let hi = useHistory();

  const [dpSelected, setDPSelected] = useState([]);
  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await DatPhongFinder.get(`/danh-sach-full/${id}`);
        setDPSelected(res.data.data.datphong);
      } catch (err) {
        console.log(err.message);
      }
    };

    fetchData();
  }, [setDPSelected, id]);
  const goBack = () => {
    hi.push("/quan-ly/phong/dat-phong");
  };
  const renderTrangThaiDP = (tt) => {
    let result;
    switch (tt + "") {
      case "0":
        result = " thanh ton";
        break;
      case "1":
        result = "ang hot ng";
        break;
      case "2":
        result = " b hy";
        break;
      default:
        break;
    }
    return result;
  };
  return (
    <div>
      <div className="d-flex flex-row">
        <button className="btn btn-primary mt-5" onClick={goBack}>
          Quay li
        </button>
        <div className="mt-5 ml-2">
          <CTDPSuDungDichVu id={id} />
        </div>
      </div>

      <h4 className="mt-3 mb-3">PHNG: {dpSelected.phong_id}</h4>
      <table className="table table-striped table-hover table-bordered mb-5">
        <tbody>
          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Tn khch hng</td>
            <td className="col-9 pl-2">{dpSelected.kh_name}</td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">S in thoi</td>
            <td className="col-9 pl-2">{dpSelected.kh_sdt}</td>
          </tr>
          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Chng minh nhn dn</td>
            <td className="col-9 pl-2">{dpSelected.kh_cmnd}</td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Trng thi</td>
            <td className="col-9 pl-2">
              <u
                className={
                  dpSelected.trangthai === 0 ? "text-success" : "text-info"
                }
              >
                {renderTrangThaiDP(dpSelected.trangthai)}
              </u>
            </td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Hnh thc t phng</td>
            <td className="col-9 pl-2">{dpSelected.hinhthucdp}</td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Ngy t phng</td>
            <td className="col-9 pl-2">
              Ngy: {NormalizeDate(dpSelected.ngaydat)}, lc{" "}
              {convertTime(dpSelected.ngaydat)}
            </td>
          </tr>
          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Check In</td>
            <td className="col-9 pl-2">
              Ngy: {NormalizeDate(dpSelected.checkin)}, lc{" "}
              {convertTime(dpSelected.checkin)}
            </td>
          </tr>
          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Check Out</td>
            <td className="col-9 pl-2">
              Ngy: {NormalizeDate(dpSelected.checkout)}, lc{" "}
              {convertTime(dpSelected.checkout)}
            </td>
          </tr>
          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Khch hng Check In</td>
            <td className="col-9 pl-2">
              {dpSelected.kh_checkin
                ? "Ngy:" +
                  NormalizeDate(dpSelected.kh_checkin) +
                  ", lc: " +
                  convertTime(dpSelected.kh_checkin)
                : "Khch hng cha check in"}
            </td>
          </tr>
          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">
              Khch hng Check Out
            </td>
            <td className="col-9 pl-2">
              {dpSelected.kh_checkout
                ? "Ngy:" +
                  NormalizeDate(dpSelected.kh_checkout) +
                  ", lc: " +
                  convertTime(dpSelected.kh_checkin)
                : "Khch hng cha check out"}
            </td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Hnh thc thu</td>
            <td className="col-9 pl-2">{dpSelected.kieuthue}</td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">
              Gi thu theo{" "}
              {dpSelected.kieuthue === "Thu theo ngy" ? "ngy" : "gi"}
            </td>
            <td className="col-9 pl-2">
              {NumberFormat(dpSelected.giathue)}&nbsp;<b>VND</b>
            </td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">
              S {dpSelected.kieuthue === "Thu theo ngy" ? "ngy" : "gi"}{" "}
              thu
            </td>
            <td className="col-9 pl-2">
              {dpSelected.sotgthue} &nbsp;
              {dpSelected.kieuthue === "Thu theo ngy" ? "ngy" : "gi"}
            </td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Tin cc</td>
            <td className="col-9 pl-2">
              {NumberFormat(dpSelected.tiencoc)}&nbsp;<b>VND</b>
            </td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">
              Tng tin s dng dch v
            </td>
            <td className="col-9 pl-2">
              {NumberFormat(dpSelected.tongtien)}&nbsp;<b>VND</b>
            </td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Tn ngi thc hin</td>
            <td className="col-9 pl-2">{dpSelected.nv}</td>
          </tr>
        </tbody>
      </table>
    </div>
  );
};

export default ChiTietDatPhong;
