import React, { useState } from "react";
import KiemTraDangNhapBackEnd from "../../apis/KiemTraDangNhapBackEnd";
import ReactIsCapsLockActive from "@matsun/reactiscapslockactive";
import ThemLichSu from "../../utils/ThemLichSu";
const LoginBackEnd = () => {
  const [ten, setTen] = useState("");
  const [mk, setMk] = useState("");
  const [messageError, setMessageError] = useState("");

  const handleSubmit = async () => {
    const english = /^[A-Za-z0-9]*$/;
    if (ten === "") {
      setMessageError("Bn phi nhp tn ti khon. Khng c  trng");
      setTimeout(() => {
        setMessageError("");
      }, 4000);
    } else if (ten.indexOf(" ") >= 0) {
      setMessageError("Tn ti khon khng c cha khong trng.");
      setTimeout(() => {
        setMessageError("");
      }, 4000);
    } else if (!english.test(ten)) {
      setMessageError("Tn ti khon khng c cha k t c bit hoc du.");
      setTimeout(() => {
        setMessageError("");
      }, 4000);
    } else if (ten.length > 0 && ten.length < 6) {
      setMessageError("Tn ti khon t nht phi c 6 k t.");
      setTimeout(() => {
        setMessageError("");
      }, 4000);
    } else if (mk === "") {
      setMessageError("Bn phi nhp mt khu. Khng c  trng");
    } else if (mk.indexOf(" ") >= 0) {
      setMessageError("Mt khu khng c cha khong trng.");
      setTimeout(() => {
        setMessageError("");
      }, 4000);
    } else if (!english.test(mk)) {
      setMessageError("Mt khu khng c cha k t c bit hoc du.");
      setTimeout(() => {
        setMessageError("");
      }, 4000);
    } else if (mk.length > 0 && mk.length < 8) {
      setMessageError("Mt khu t nht phi c 8 k t.");
      setTimeout(() => {
        setMessageError("");
      }, 4000);
    } else {
      try {
        const res = await KiemTraDangNhapBackEnd.post("/", {
          ten: ten,
          mk: mk,
          vaitro: "KH",
        });
        // console.log(typeof res.data.data.ten_ht);
        if (res.data.status !== "ok") {
          setMessageError(res.data.status);
          setTimeout(() => {
            setMessageError("");
          }, 3000);
        } else {
          window.localStorage.setItem("dangnhap", "true");
          window.localStorage.setItem("user_name", res.data.data.ten);
          window.localStorage.setItem("user_displayname", res.data.data.ten_ht);
          window.localStorage.setItem("user_role", res.data.data.vaitro);
          window.localStorage.setItem("user_avt", res.data.data.avt);

          ThemLichSu({
            doing: "ng nhp",
            olddata: {},
            newdata: {},
            tbl: "Ngi dng",
          });
          window.location.reload();
        }
      } catch (err) {
        console.error(err.message);
      }
    }
  };
  return (
    <div className="row d-flex justify-content-center mt-5">
      <div className="col-sm-4 mt-5 border border-5 rounded px-5 pb-5 border-mute  ">
        <h1 className="text-center mt-4 mb-5">ng nhp</h1>

        <form action="" className="need-validated mx-4">
          <div className="input-group mb-3">
            <div className="input-group-prepend">
              <span className="input-group-text">
                <i className="fas fa-user text-primary"></i>
              </span>
            </div>
            <input
              type="text"
              placeholder="Nhp tn ti khon"
              id="username"
              className="form-control"
              title="Vui lng in ti khon vo y"
              value={ten}
              onChange={(e) => {
                setTen(e.target.value);
              }}
              required
            />
          </div>
          <div className="input-group mb-3">
            <div className="input-group-prepend">
              <span className="input-group-text">
                <i className="fas fa-key text-primary"></i>
              </span>
            </div>
            <input
              type="password"
              placeholder="Nhp mt khu"
              id="password"
              className="form-control"
              title="Vui lng in mt khu vo y"
              value={mk}
              onChange={(e) => {
                setMk(e.target.value);
              }}
              required
            />
          </div>

          <p className="text-danger">{messageError}</p>
          <ReactIsCapsLockActive>
            {(active) => (
              <p className="text-danger">
                {" "}
                {active ? "Caps lock ang bt. Vui lng tt" : ""}
              </p>
            )}
          </ReactIsCapsLockActive>
          {/* <div className="form-check mb-3">
            <label className="form-check-label">
              <input
                type="checkbox"
                className="form-check-input"
                value={checked}
                onChange={(e) => setChecked(e.target.checked)}
              />
              Nh mt khu
            </label>
          </div> */}
          <button
            type="button"
            className="btn btn-primary form-control py-2"
            onClick={handleSubmit}
          >
            ng nhp
          </button>
        </form>
      </div>
    </div>
  );
};

export default LoginBackEnd;
