import React, { useEffect, useState } from "react";
import { useParams, useHistory } from "react-router";
import DatPhongFinder from "../../../apis/DatPhongFinder";
import KhachHangFinder from "../../../apis/KhachHangFinder";
import PhongFinder from "../../../apis/PhongFinder";
import {
  NormalizeDate,
  convertTime,
  NumberFormat,
} from "../../../utils/DataHandler";
import ThemLichSu from "../../../utils/ThemLichSu";
import DoiPhong from "./doihuy/DoiPhong";
import HuyPhong from "./doihuy/HuyPhong";
const CheckIn = () => {
  const { phongid } = useParams();
  let hi = useHistory();
  const [dpSelected, setDpSelected] = useState([]);

  const [tenKh, setTenKh] = useState("");
  const [cmndKH, setCmndKh] = useState("");
  const [sdtKh, setSdtKh] = useState("");
  const [khId, setKhId] = useState("");
  const [dpId, setDpId] = useState("");
  const [msgError, setMsgError] = useState("");

  const [old, setOld] = useState({});
  useEffect(() => {
    const getDatPhong = async () => {
      try {
        const res = await DatPhongFinder.get(`/danh-sach/${phongid}`);
        if (res.data.status) {
          setDpSelected(res.data.data.datphong);
          const datPhongSelected = res.data.data.datphong;
          setTenKh(datPhongSelected.kh_name);
          setCmndKh(datPhongSelected.kh_cmnd);
          setSdtKh(datPhongSelected.kh_sdt);
          setKhId(datPhongSelected.kh_id);
          setDpId(datPhongSelected.id);
          setOld({
            id: datPhongSelected.kh_id,
            ten: datPhongSelected.kh_name,
            cmnd: datPhongSelected.kh_cmnd,
            sdt: datPhongSelected.kh_sdt,
          });
        }
      } catch (err) {
        console.log(err.message);
      }
    };
    getDatPhong();
  }, [phongid]);

  const goBack = () => {
    hi.push("/quan-ly/phong/tinh-trang");
  };

  const handleSave = async () => {
    if (!tenKh) {
      setMsgError("Tn khch hng khng c  trng");
      setTimeout(() => {
        setMsgError("");
      }, 3000);
      //
    } else if (!cmndKH) {
      setMsgError("Chng minh nhn dn khng c  trng");
      setTimeout(() => {
        setMsgError("");
      }, 3000);
      //
    } else if (cmndKH.length !== 12) {
      setMsgError("Chng minh nhn dn khng hp l. (gm 12 s). ");
      setTimeout(() => {
        setMsgError("");
      }, 3500);
    } else if (!sdtKh) {
      setMsgError("S in thoi khng c  trng");
      setTimeout(() => {
        setMsgError("");
      }, 3000);
      //
    } else if (sdtKh.length < 10 || sdtKh.length > 11) {
      setMsgError(
        "S in thoi khng hp l. (gm 10 s vi di ng, 11 s vi my bn)."
      );
      setTimeout(() => {
        setMsgError("");
      }, 5000);
    } else {
      try {
        const res = await KhachHangFinder.put("/sua-checkin", {
          id: khId,
          ten: tenKh,
          cmnd: cmndKH,
          sdt: sdtKh,
        });
        if (res.data.status === "ok") {
          const newd = {
            id: khId,
            ten: tenKh,
            cmnd: cmndKH,
            sdt: sdtKh,
          };

          if (JSON.stringify(newd) !== JSON.stringify(old)) {
            ThemLichSu({
              doing: "Sa",
              olddata: { old },
              newdata: {
                new: newd,
              },
              tbl: "Khch hng",
            });
          }
          hi.push("/quan-ly/phong/tinh-trang");
        }
      } catch (err) {
        console.log(err.message);
      }
    }
  };

  const handleCheckIn = async () => {
    if (!tenKh) {
      setMsgError("Tn khch hng khng c  trng");
      setTimeout(() => {
        setMsgError("");
      }, 3000);
      //
    } else if (!cmndKH) {
      setMsgError("Chng minh nhn dn khng c  trng");
      setTimeout(() => {
        setMsgError("");
      }, 3000);
      //
    } else if (cmndKH.length !== 12) {
      setMsgError("Chng minh nhn dn khng hp l. (gm 12 s). ");
      setTimeout(() => {
        setMsgError("");
      }, 3500);
    } else if (!sdtKh) {
      setMsgError("S in thoi khng c  trng");
      setTimeout(() => {
        setMsgError("");
      }, 3000);
      //
    } else if (sdtKh.length < 10 || sdtKh.length > 11) {
      setMsgError(
        "S in thoi khng hp l. (gm 10 s vi di ng, 11 s vi my bn)."
      );
      setTimeout(() => {
        setMsgError("");
      }, 5000);
    } else {
      try {
        await KhachHangFinder.put("/sua-checkin", {
          id: khId,
          ten: tenKh,
          cmnd: cmndKH,
          sdt: sdtKh,
        });
        const newd = {
          id: khId,
          ten: tenKh,
          cmnd: cmndKH,
          sdt: sdtKh,
        };

        if (JSON.stringify(newd) !== JSON.stringify(old)) {
          ThemLichSu({
            doing: "Sa",
            olddata: { old },
            newdata: {
              new: newd,
            },
            tbl: "Khch hng",
          });
        }

        await DatPhongFinder.put("/check-in", {
          id: dpId,
        });
        ThemLichSu({
          doing: "Check In",
          olddata: {},
          newdata: {},
          tbl: "Phng",
        });

        await PhongFinder.put("/update-tt", {
          ten: phongid,
          trangthai: 2,
        });

        hi.push(`/quan-ly/phong/tinh-trang/${phongid}/su-dung-dich-vu`);
      } catch (err) {
        console.log(err.message);
      }
    }
  };

  return (
    <div className="mt-5">
      <h1 className="text-center mb-5">THNG TIN T PHNG</h1>

      <div className="form-row">
        <div className="col-6">
          <div className="form-group">
            <label htmlFor="tenkh">Tn khch hng</label>
            <input
              type="text"
              id="tenkh"
              className="form-control"
              value={tenKh}
              onChange={(e) => setTenKh(e.target.value)}
            />
          </div>
        </div>
        <div className="col-3">
          <div className="form-group">
            <label htmlFor="cmnd">Chng minh nhn dn</label>
            <input
              type="text"
              className="form-control"
              id="cmnd"
              onChange={(e) => setCmndKh(e.target.value)}
              value={cmndKH}
            />
          </div>
        </div>
        <div className="col-3">
          <div className="form-group">
            <label htmlFor="sdt">S in thoi</label>
            <input
              type="text"
              className="form-control"
              id="sdt"
              onChange={(e) => setSdtKh(e.target.value)}
              value={sdtKh}
            />
          </div>
        </div>
      </div>
      <p className="text-danger">{msgError}</p>
      <div className="d-flex flex-row">
        <button className="btn btn-primary  mt-3 mr-2" onClick={handleCheckIn}>
          Check In
        </button>
        <button className="btn btn-info px-4 mt-3 mr-2" onClick={handleSave}>
          Lu
        </button>
        <button className="btn btn-secondary mt-3" onClick={goBack}>
          Quay li
        </button>
        <DoiPhong phongid={phongid} dpid={dpId} />
        <HuyPhong phongid={phongid} dpid={dpId} tiencoc={dpSelected.tiencoc} />
      </div>

      <h4 className="mt-3 mb-3">PHNG : {dpSelected.phong_id}</h4>
      <table className="table table-striped table-hover table-bordered mb-5">
        <tbody>
          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Hnh thc t phng</td>
            <td className="col-9 pl-2">{dpSelected.hinhthucdp}</td>
          </tr>
          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Ngy t phng</td>
            <td className="col-9 pl-2">
              {" "}
              Ngy: {NormalizeDate(dpSelected.ngaydat)}, lc{" "}
              {convertTime(dpSelected.ngaydat)}
            </td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Check In</td>
            <td className="col-9 pl-2">
              {" "}
              Ngy: {NormalizeDate(dpSelected.checkin)}, lc{" "}
              {convertTime(dpSelected.checkin)}
            </td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Check out</td>
            <td className="col-9 pl-2">
              {" "}
              Ngy: {NormalizeDate(dpSelected.checkout)}, lc{" "}
              {convertTime(dpSelected.checkout)}
            </td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Nhn vin tip nhn</td>
            <td className="col-9 pl-2">{dpSelected.nv}</td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Hnh thc thu</td>
            <td className="col-9 pl-2">{dpSelected.kieuthue}</td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Gi thu </td>
            <td className="col-9 pl-2">
              {NumberFormat(dpSelected.giathue)} VND /{" "}
              {dpSelected.kieuthue === "Thu theo ngy" ? "ngy" : "gi"}
            </td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">
              S {dpSelected.kieuthue === "Thu theo ngy" ? "ngy" : "gi"}{" "}
              thu{" "}
            </td>
            <td className="col-9 pl-2">
              {dpSelected.sotgthue}&nbsp;
              {dpSelected.kieuthue === "Thu theo ngy" ? "ngy" : "gi"}
            </td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Nhn vin tip nhn</td>
            <td className="col-9 pl-2">{dpSelected.nv}</td>
          </tr>

          <tr className="d-flex">
            <td className="col-3 pl-2 font-weight-bold">Tin cc</td>
            <td className="col-9 pl-2">{NumberFormat(dpSelected.tiencoc)}</td>
          </tr>
        </tbody>
      </table>
    </div>
  );
};

export default CheckIn;
